
#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0px;
  background: url(data:image/svg+xml;base64,) no-repeat center center fixed;
  background-size: cover;
}
body{
  background: linear-gradient(10deg,rgb(255, 118, 54), rgb(255, 137, 107)) no-repeat center center fixed;
  background-size: cover;
  margin: 0px;
  padding: 0px;
}
h1{
  color:white;
  font-weight: 800;
}
h2{
  color:rgba(255, 255, 255, 0.719);
}
@media screen and (max-device-width: 1024px){
body{
    background: linear-gradient(10deg,rgb(139, 185, 255), rgb(225, 107, 255));
}
#app{
    background-position: 0 0;
}
}

.header-button{
    background: rgb(0, 255, 183);
    border-radius: 100px;
    color:white;
    border:none;
    font-weight: 600;
    padding:1rem;
    font-size: 2rem;
}
.header-button-empty{
    background: none;
    border-radius: 100px;
    -webkit-box-shadow: 0 0 0 4px white inset;
            box-shadow: 0 0 0 4px white inset;
    color:white;
    border:none;
    font-weight: 600;
    padding:1rem;
    font-size: 2rem;
}
.logo{
    width: 10rem;
}
.title{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.header{
    padding-top:2rem;
    margin:0px;
}
.headline{
    color:white;
    font-size: 5rem;
    padding: 2rem;
    font-weight: 100;
}
.name{
    color: white;
    font-weight: 900;
    font-size: 4rem;
    text-align: left;
    padding-left: 2rem;
}
@media screen and (max-device-width: 1024px){
.headline{
    font-size: 3rem;  
    padding: 3rem 0.5rem;
}
.name{
    font-size:4rem;
    text-align: center;
    padding:0px;
}
.title{
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
}


.hunk-white{
  height: 500px;
  background-color:rgba(255, 255, 255, 0.877);
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}
.hunk-clear{
  height: 500px;
  background-color:none;
  width:100vw;
  padding: 1rem 0;
  margin-top: 1rem;
}


.projects{
        min-height: 500px;
        background-color:rgba(255, 255, 255, 0.877);
        width:100vw;
        padding: 1rem 0;
        margin: 1rem 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: flex-start;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
}
.project{
        width:33.33vw;
}
.project img{
        width:100%;
}
@media screen and (max-device-width: 1024px){
.project{
        width: 50%;
}
.projects{
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
}
